    box-shadow: -5.985px -0.419px 30px 0 rgba(0, 0, 0, 0.08), inset -2px 0 10px 0 rgba(0, 0, 0, 0.04);
    width: 52px;
    height: auto;
    z-index: 108;
    padding: 0 0 17px;
    border-radius: 13px 0 0 13px; }
    [dir="rtl"] .brk-theme-options__panel {
